import { ProfileAPI } from "../API/API"

const ADD_POST = 'ADD-POST'
const POST_TEXT_CHANGE = 'POST-TEXT-CHANGE'

const SET_USER_PROFILE = 'SET-USER-PROFILE'
const SET_USER_STATUS = 'SET-USER-STATUS'
const SET_USER_PHOTO = 'SET-USER-PHOTO'


let InitialState = {
         posts:[
         {id:1,text:'133ere',likeCount:21 },
         {id:2,text:'233ere',likeCount:21 },
         {id:3,text:'333ere',likeCount:21 }
         ],
         NewPostText:[''],
         usersProfiles:[],
         Status:""
        }

let ProfileReduser =(state=InitialState,action)=>{
    
switch (action.type){
case ADD_POST: 
    {
let newPost= {id:9,text:state.NewPostText,likeCount:0 }
let StateCopy={...state}
StateCopy.posts=[...state.posts]

StateCopy.posts.push(newPost)
StateCopy.NewPostText=''
return StateCopy
    }
case POST_TEXT_CHANGE:
{
let StateCopy={...state}
StateCopy.NewPostText=action.text
return StateCopy
}

case SET_USER_PROFILE:
{return { ...state, usersProfiles:[action.userProfile]}}

case SET_USER_STATUS:
{return { ...state, Status:action.userStatus}}

case SET_USER_PHOTO:
    debugger
    // photos
    // large
    // small
{return { ...state, usersProfiles:{...state.usersProfiles,0:{...state.usersProfiles[0],photos:{large:action.userPhoto,small:action.userPhoto}}}}}

default: return state
}}

export const AddPostCreator=()=> {
return  {type:ADD_POST}}

export const PostTextChangeCreator=(text)=> {
return  {type:POST_TEXT_CHANGE,text:text}}

export const setUserProfile = (userProfile) => {
        return { type: SET_USER_PROFILE,userProfile }
  }

export const setUserStatus = (userStatus) => {
    return { type: SET_USER_STATUS,userStatus}
}

export const setUserPhoto = (userPhoto) => {
    return { type: SET_USER_PHOTO,userPhoto}
}

export const getUserProfile =(userId)=>{
return (dispatch)=>{
    ProfileAPI.getUserProfile(userId).then(Response => { 
    dispatch(setUserProfile(Response)) })
}}

export const getUserStatus =(userId)=>{
    return (dispatch)=>{
        ProfileAPI.getUserStatus(userId).then(Response => { 
        dispatch(setUserStatus(Response)) })
    }}

export const putUserStatus =(status)=>{
    return (dispatch)=>{
        ProfileAPI.putUserStatus(status).then(Response => {
            if (Response.data.resultCode===0){
             dispatch(setUserStatus(status))} })
         }}

export const putUserPhoto = (userPhoto) => {
    return (dispatch)=>{
        ProfileAPI.putUserPhoto(userPhoto).then(Response => {
            if (Response.data.resultCode===0){
                dispatch(setUserPhoto(userPhoto))} })
         }}

export const setProfileData = (value) => {
    return (dispatch,getState) => {
        const Profile = {...getState().ProfilePage.usersProfiles[0],...value }
        debugger
                ProfileAPI.putProfile(Profile).then(Response => {
            if (Response.data.resultCode === 0) {
                dispatch(getUserProfile(getState().AuthPage.id))
            }
        })
    }
}
         





export default ProfileReduser


