import React from 'react'

class ProfileInfoItem extends React.Component  {
    state = {
        isRedacting: false,
        Text: this.props.value
    }

    // componentDidUpdate(prevProps,prevState){
    //     if (prevProps.status !== this.props.status){this.setState({StatusText:this.props.status })}
    //     }

    redactorOn = () => {
        this.setState({ isRedacting: true }) 
    }

    redactorOff = (e) => {
        this.setState({ isRedacting: false })
        this.props.setProfileData({[this.props.name]:e.currentTarget.value})
    }

    onChange=(e)=>{
        this.setState({Text:e.currentTarget.value})}
            
        render() {
              return (
                <div>

            {this.props.isMyProfile   ? 
            <div>
            {!this.state.isRedacting && <div onDoubleClick={this.redactorOn} > {this.props.text}  {this.props.value}</div>}
            {this.state.isRedacting && <input type={this.props.type} autoFocus={true} onBlur={this.redactorOff} onChange={this.onChange} value={this.state.Text}/>}
            </div> :
           <div> {this.props.text} {this.props.status}</div>
        }
        </div>
        )}
    }


export default ProfileInfoItem