import { Formik, Form, Field  } from 'formik';
import * as yup from  "yup"
import React from 'react'
import s from './ProfileInfo.module.css'
import { Input } from '../../Common/FormControls/FormsControl';

class ProfileInfoContacts extends React.Component  {
    state={
        isContactsRedacting: false
        } 
        
        RedactingOn= () =>{
            this.setState({isContactsRedacting:true})
        }

        RedactingOff= () =>{
            this.setState({isContactsRedacting:false})
        }
          
render(){

   const Contact = ({ contactTitle, contactValue }) => {
        return (
            <div>
                {contactValue != null ? <div className={s.contact}><b>{contactTitle}</b>:{contactValue}</div> : ""}
            </div>
        )
    }

    const validationSchema = {};
    Object.keys(this.props.contacts).forEach((element) => {validationSchema[element] = yup.string().nullable().min(3,"3").max(25,"10")})
        
    return (
       <div>
        {!this.state.isContactsRedacting &&  <div>
            {Object.keys(this.props.contacts).map(key => {
            return (<Contact contactTitle={key} contactValue={this.props.contacts[key]} />)
            })}
             {<button onClick={this.RedactingOn}>Edit</button>}
            </div>}
            
            {this.state.isContactsRedacting &&  <div>
           
<Formik 
onSubmit={
    (values,submitProps) => {
        // alert(JSON.stringify(values, null))
        this.props.setProfileData({contacts:values})
            // ,submitProps.setStatus)
    // submitProps.resetForm()
    this.RedactingOff()
    }}
initialValues={this.props.contacts} 
validationSchema= {yup.object(validationSchema)}


>

{({status}) => (  <Form>
    <button type="submit">Save</button>

            {Object.keys(this.props.contacts).map(key => {
            return (
            <Field name={key} placeholder={key} component={Input} />)
            })}
{/* 
<div>{ (status) ? status.error[0] :"" }</div>
 */}
</Form>)}
</Formik> 
</div>}
</div>
    )
}
}

export default ProfileInfoContacts