import React from 'react';
import s from './ProfileInfo.module.css'
import Loader from '../../../assets/Img/Loader.gif';
import ProfileInfoStatus from './ProfileInfoStatus';
import ProfileInfoPhoto from './ProfileInfoPhoto';
import ProfileInfoContacts from './ProfileInfoContacts';
import ProfileInfoItem from './ProfileInfoItem';

const ProfileInfo = (props) => {
  
   
        if (props.userProfile != undefined) {
      return (
         <div>
            <div className={s.DesriptionBlock}>

               <div className={s.name}>
               <ProfileInfoItem name={'fullName'} text={''} value={props.userProfile.fullName} isMyProfile={props.isMyProfile} setProfileData={props.setProfileData}/>
               </div>
               
               <div className={s.description} >
                  <ProfileInfoStatus status={props.userStatus} putUserStatus={props.putUserStatus}  isMyProfile={props.isMyProfile}/>
                  <ProfileInfoItem  name={'aboutMe'} text={'aboutMe:'} value={props.userProfile.aboutMe} isMyProfile={props.isMyProfile} setProfileData={props.setProfileData}/>
               </div>

               <div className={s.avatar}>
               <ProfileInfoPhoto Photo={props.userProfile.photos.large} isMyProfile={props.isMyProfile} putUserPhoto={props.putUserPhoto}/>
               </div>
               
               
               <div className={s.job}>
               <ProfileInfoItem type={'checkbox'} name={'lookingForAJob'} text={'Ищу работу:'} value={props.userProfile.lookingForAJob} isMyProfile={props.isMyProfile} setProfileData={props.setProfileData}/>
               <ProfileInfoItem  name={'lookingForAJobDescription'} text={'Ищу работу:'} value={props.userProfile.lookingForAJobDescription} isMyProfile={props.isMyProfile} setProfileData={props.setProfileData}/>
{/* 
   Ищу работу:{props.userProfile.lookingForAJob}<br />
                  {props.userProfile.lookingForAJobDescription}<br /> */}
               </div>

               <div className={s.contacts}>
                  <b>Контакты:</b>
                  <ProfileInfoContacts
                  isMyProfile={props.isMyProfile} 
                  contacts={props.userProfile.contacts}
                  setProfileData={props.setProfileData}
                  />
                  
               </div>
               <br />
               <br />

            </div>
         </div>
      )
   }
   else {
      return (
         <div>
            <img className={s.loader} src={Loader} />
         </div>)
   }
}
export default ProfileInfo;