import React from 'react';
import defultAvatar from '../../../assets/Img/avatar.png';
import s from './ProfileInfo.module.css'

class ProfileInfoPhoto extends React.Component {
   state={
      isPhotoChange:false
   }

   putUserPhoto=(e)=>{
        this.props.putUserPhoto(e.target.files[0])
        e.target.value=''
        this.setState({isPhotoChange:false}) 
   }
   showPhotoChange=()=>{
        this.setState({isPhotoChange:true}) 
        }

render(){
     return (
        <div>
   {(this.props.Photo === null)
                  ? <img className={s.avatar} src={defultAvatar} onClick={this.showPhotoChange} />
                  : <img className={s.avatar} src={this.props.Photo} onClick={this.showPhotoChange} ></img>}
               <br />
               {this.props.isMyProfile && (this.state.isPhotoChange &&  <input type="file" onChange={this.putUserPhoto} /> )}
        </div>
    )}
}

export default ProfileInfoPhoto